import React,{useEffect,useState} from "react";

import { Messages, Status, ChatInput,Sidebar } from "../../components";
import { Dialogs } from "../../containers";

import { observer, inject} from 'mobx-react';
import axios from "axios";
import "./Chat.scss";
import { DialogTitle } from '@material-ui/core/DialogTitle';
import { Link,Redirect } from "react-router-dom";
const Chat = observer(({authStore,chatStore}) => {

  const [isLoading,setIsLoading]=useState(true)


  useEffect(()=>{
    if(authStore.current_user){
      chatStore.fetchDialogs()
    } else {
      authStore.fetchProfile()
    }
    console.log('authStore',authStore.current_user)
  },[authStore.current_user])
  if(!authStore.access_token){
    return <Redirect push to="/login" />
  }
  if(authStore.current_user===null) return <h1>Loading...</h1>
  return (
  <section className="home">
    <div className="chat">
      <div className="chat__sidebar">
        {/* <div className="chat__sidebar-header">
          <div>
            <Icon type="team" />
            <span> </span>
          </div>
          <Button type="link" shape="circle" icon="form" />
        </div> */}
        <Sidebar userId={authStore.current_user.id} fetchDialods={()=>chatStore.fetchDialogs()}/>
        <div className="chat__sidebar-dialogs">
          <Dialogs userId={authStore.current_user.id} isLoading={chatStore.dialogsIsLoading} selectedDialog={chatStore.selectedDialog} items={chatStore.dialogs} />
        </div>
      </div>
      <div className="chat__dialog">
        <div className="chat__dialog-header">
          <div />
          <div className="chat__dialog-header-center">
            <b className="chat__dialog-header-username">
              {chatStore.selectedDialog
              ?chatStore.dialogs.find(d=>d.id===chatStore.selectedDialog).user.username
              :authStore.current_user.username
              }
            </b>
            <div className="chat__dialog-header-status">
              <Status 
              user={chatStore.selectedDialog
              ?chatStore.dialogs.find(d=>d.id===chatStore.selectedDialog).user
              :{isOnline:true}
              }  
              />
            </div>
          </div>
          <div></div>   {/* <Button type="link" shape="circle" icon="ellipsis" /> */}
        </div>
        {authStore.current_user.id&&<Messages isLoading={chatStore.dialogIsLoading} userId={authStore.current_user.id} items={chatStore.selectedDialog&&chatStore.messages}/>}
        <div className="chat__dialog-input">
          <ChatInput send={(text)=>chatStore.sendMassage({text,id:chatStore.selectedDialog})}/>
        </div>
      </div>
    </div>
  </section>
)});

export default inject('authStore','chatStore')(Chat);
