import React from "react";
import classNames from "classnames";
import format from "date-fns/format";
import isToday from "date-fns/is_today";
import { observer, inject} from 'mobx-react';
import { IconReaded } from "../";
import Avatar from '@material-ui/core/Avatar';
import Badge from '@material-ui/core/Badge';
import reactStringReplace from 'react-string-replace';
import { Emoji,NimbleEmoji } from 'emoji-mart';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import customEmojisObj from '../../customEmojis'
const getMessageTime = created_at => {
  if (isToday(created_at)) {
    return format(created_at, "HH:mm");
  } else {
    return format(created_at, "DD.MM.YYYY");
  }
};
const StyledBadge = withStyles((theme) => ({
  badge: {
    backgroundColor: '#44b700',
    color: '#44b700',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
  }
}))(Badge);
const useStyles = makeStyles((theme) => ({
  badge: {
    backgroundColor: '#44b700',
    color: '#44b700',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
  }
}));
const DialogItem = observer(({chatStore, user, undread, updatedAt, text, isMe, id}) => (
  <div
    onClick={()=>chatStore.setDialog(id)}
    className={classNames("dialogs__item", {
      "dialogs__item--online": user.isOnline
    })}
  >
    <div className="dialogs__item-avatar">
      <Avatar alt={user.username} src={user.imgUrl} />
    </div>
    <div className="dialogs__item-info">
      <div className="dialogs__item-info-top">
        <b>{user.username}</b>
        <span>{getMessageTime(updatedAt)}</span>
      </div>
      <div className="dialogs__item-info-bottom">
        <p>
        {reactStringReplace(text, /:(.+?):/g, (match, i) => (
            customEmojisObj[match]
            ?<img  className="emoji" key={i} style={{width:16,height:16,backgroundSize:'contain',backgroundImage:`url(${customEmojisObj[match].imageUrl})`}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
            :<img  className="emoji" key={i} style={{...Emoji({emoji:match,set:"twitter",size:16,css:true})}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
          ))}
        </p>
        {isMe && <IconReaded isMe={true} isReaded={false} />}
        {undread > 0 && (
          <div className="dialogs__item-info-bottom-count">
            {undread > 9 ? "+9" : undread}
          </div>
        )}
      </div>
    </div>
  </div>
));

export default inject('chatStore')(DialogItem);
