import React from "react";
import orderBy from "lodash/orderBy";
import { Empty } from "antd";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/Inbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import { DialogItem } from "../";
import 'antd/es/empty/style/index.css';
import "./Dialogs.scss";

const Dialogs = ({ items, userId, onSearch, inputValue,selectedDialog }) => (
  <div className="dialogs">
    <div className="dialogs__search">
      <input
        placeholder="  "
        onChange={e => onSearch(e.target.value)}
        value={inputValue}
      ></input>
    </div>
    <List component="nav" aria-label="main mailbox folders">
    {items.length ? (
      orderBy(items, ["createdAt"], ["desc"]).map(item => <div onClick={()=>console.log(item.id)}>
        <ListItem style={selectedDialog==item.id?{padding:4,background: '#4CAF50',color:' #fff'}:{padding:4}} button>
          <DialogItem key={item.id} 
          id={item.id} 
          user={item.user}
          //  undread={10} 
          // isMe={selectedDialog==item.id}
          created_at={item.updatedAt} 
          text={item.text} 
          // isMe={item.user.id === userId}
          {...item} />
         </ListItem>
      </div>)
    ) : (
      <Empty
        image={Empty.PRESENTED_IMAGE_SIMPLE}
        description="  "
      />
    )}
    </List>
  </div>
);

export default Dialogs;
