const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default ({
  fetchDialods,
  user,
  userId,
  onChangeTextArea,
  onModalOk,
}) => {
  const [open, setOpen] = React.useState(false);
  const [isLoading,setIsLoading]=useState(false)
  const [users,setUsers]=useState([])
  const fetchFriends = async () => {
    setIsLoading(true)
    console.log('fetchFriends', userId)
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/friend/${userId}`)
      console.log(' Returned fetchFriends data:', response.data.list.map(el=>el.reseiver));
      setUsers(response.data.list.map(el=>el.reseiver))
    } catch (e) {
      console.log(` Axios request failed: ${e}`);
    }
    setIsLoading(false) 
  }
  const [selectedUserId,setSelectedUserId]=useState(null)
  const [inputValue,setInputValue]=useState('')
  const createDialog = async () => {
    console.log(selectedUserId)
    setIsLoading(true)
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/chat`,{users:[selectedUserId]})
      console.log(' Returned dial data:', response.data);
    } catch (e) {
      console.log(` Axios request failed: ${e}`);
    }
    fetchDialods()
    setIsLoading(false)
  }
  useEffect(()=>{
    fetchFriends(userId)
  },[])
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className="chat__sidebar">
      <div className="chat__sidebar-header">
        <div>
          {/* <Icon type="team" /> */}
          <span> </span>
        </div>
        <IconButton onClick={handleClickOpen} aria-label="delete">
          <ForumIcon />
        </IconButton>
      </div>
      {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Open dialog
      </Button> */}
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
           
        </DialogTitle>
        <DialogContent dividers>
          <FormControl style={{ width: '100%', minWidth:500 }} variant="outlined" className="add-dialog-form">
            <InputLabel htmlFor="outlined-age-native-simple"> </InputLabel>
            <Select
              native
              value={selectedUserId}
              // onSelect={(v)=>{setSelectedUserId(v)}}
              onChange={(e)=>setSelectedUserId(e.target.value)}
              // onChange={(_,v)=>setInputValue(v.children)}
              label=" "
              inputProps={{
                name: 'age',
                id: 'outlined-age-native-simple',
              }}
            >
              {/* <option aria-label="None" value="" />
              <option value={10}>Ten</option>
              <option value={20}>Twenty</option>
              <option value={30}>Thirty</option> */}
              <option aria-label="None" value="" />
              {users.map(user => <option key={user.id} value={user.id}>{user.username}</option>)}
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button key="back" onClick={handleClose}>
            
          </Button>
          <Button
            disabled={!selectedUserId||isLoading}
            key="submit"
            color="primary"
            // loading={isLoading}
            onClick={createDialog}>
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}