import React, { useState } from "react";
import PropTypes from "prop-types";
import EmojiEmotionsOutlinedIcon from '@material-ui/icons/EmojiEmotionsOutlined';
import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import AttachFileOutlinedIcon from '@material-ui/icons/AttachFileOutlined';
import "./ChatInput.scss";
import "./emiji.css";
import 'emoji-mart/css/emoji-mart.css'
import { Picker } from "emoji-mart";
import IconButton from '@material-ui/core/IconButton';
import { Emoji,NimbleEmoji } from 'emoji-mart';
import reactStringReplace from 'react-string-replace';
import { replace } from "lodash";
import customEmojisObj from '../../customEmojis'
const i18n = {
  search: '',
  clear: '', // Accessible label on "clear" button
  notfound: ' ',
  // skintext: 'Choose your default skin tone',
  categories: {
    search: ' ',
    recent: ' ',
    smileys: '',
    people: '',
    nature: '',
    foods: '  ',
    activity: '  ',
    places: '',
    objects: '',
    symbols: '',
    flags: '',
    custom: '',
  },
  // categorieslabel: 'Emoji categories', // Accessible title for the list of categories
  // skintones: {
  //   1: 'Default Skin Tone',
  //   2: 'Light Skin Tone',
  //   3: 'Medium-Light Skin Tone',
  //   4: 'Medium Skin Tone',
  //   5: 'Medium-Dark Skin Tone',
  //   6: 'Dark Skin Tone',
  // },
}


const customEmojis = Object.values(customEmojisObj)


const ChatInput = props => {
  const [value, setValue] = useState("");
  const [visiblePicker, setVisiblePicker] = useState(false);


  const addEmoi = (e) => {

    setValue(`${value}${e.colons}`)
  }

  const send = () => {
    let text = document.querySelector("#input-box").innerHTML.replace(/<img.*?alt="(.*?)"[^\>]+>/g, '$1');
    console.log(text)
    text = text.replace(/<[^>]+>/g, ' ')
    text = text.replace(/&nbsp;/g, ' ')
    document.querySelector("#input-box").innerHTML=""
    return text.trim()
  }
  return (
    <div className="chat-input">
      <div className="chat-input__smile-btn">
        <IconButton onClick={()=>setVisiblePicker(!visiblePicker)} aria-label="delete">
          <EmojiEmotionsOutlinedIcon />
        </IconButton>
          {visiblePicker&&<div className="chat-input__emoji-picker">
          <Picker custom={customEmojis} i18n={i18n} showPreview={false} color={"#2121212"} showSkinTones={false} style={{visibility: visiblePicker?'visible':'hidden'}} onSelect={addEmoi} set="twitter" title='Pick your emoji' emoji='point_up' />
          </div>}
      </div>

        <div id="input-container">
        <div id="input-box" onPaste={(e)=>{
            e.preventDefault()
            console.log(e.clipboardData)
            let text = e.clipboardData.getData('text/plain')
            // setValue(`${value}${text}`)
            const htmlText = (text.replace(/:(.+?):/g, (match, i) => 
              {
                if(match.length>3){
                  const cm = customEmojisObj[match.slice(1,-1)]
                  if(cm){
                    return `<img alt="${match}" class="emoji" style='width: 22px;height: 22px;background-size: contain;background-image: url(${cm.imageUrl});' src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==">`
                  }
                }
                const css = convertStyleToCSS(Emoji({emoji:match,set:"twitter",size:22,css:true}))
                return `<img alt="${match}" class="emoji" style='${css}' src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==">`
              }
            ))
            document.execCommand('insertHTML', null, htmlText)
        }} class="single-line" contenteditable="true" onKeyDown={e=>{
          if ( e.keyCode == 13 ){
            e.preventDefault();
            props.send(send())
          }
        }}>
          {reactStringReplace(value, /:(.+?):/g, (match, i) => (
            customEmojisObj[match]
            ?<img  className="emoji" key={i} style={{width:22,height:22,backgroundSize:'contain',backgroundImage:`url(${customEmojisObj[match].imageUrl})`}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
            :<img  className="emoji" key={i} style={{...Emoji({emoji:match,set:"twitter",size:22,css:true})}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
          ))}
        </div>
        </div>

        {/* <div contentEditable={true} className='ant-input ant-input-lg'>
          Looks good to me

          <span contentEditable={false} dangerouslySetInnerHTML={{
            __html: Emoji({
              html: true,
              set: 'apple',
              emoji: '+1',
              size: 24
            })
          }}></span>
        </div> */}
      {/* <div onChange={e => setValue(e.target.value)} contenteditable="true" className='ant-input ant-input-lg'>
        {value}
        {reactStringReplace(value, /:(.+?):/g, (match, i) => (
          <Emoji onChange={e => setValue(e.target.value)} key={i} emoji={match} set="apple" size={16} />
        ))}
      </div> */}
      <div className="chat-input__actions">
        {/* <IconButton onClick={()=>props.send(send())} aria-label="delete">
          <AttachFileOutlinedIcon />
        </IconButton> */}
        <IconButton onClick={()=>props.send(send())} aria-label="delete">
          <CheckCircleOutlineOutlinedIcon />
        </IconButton>
      </div>
    </div>
  );
};

ChatInput.propTypes = {
  className: PropTypes.string
};

export default ChatInput;

function convertStyleToCSS(style) {
  var div = document.createElement('div');

  for (var key in style) {
    var value = style[key];

    if (!isNaN(value - parseFloat(value))) {
      value += 'px';
    }

    div.style[key] = value;
  }

  return div.getAttribute('style');
};