import React from "react";
import PropTypes from "prop-types";
import distanceInWordsToNow from "date-fns/distance_in_words_to_now";
import ruLocale from "date-fns/locale/ru";
import classNames from "classnames";
import readedSvg from "../../assets/img/readed.svg";
import noReadedSvg from "../../assets/img/noreaded.svg";
import Avatar from '@material-ui/core/Avatar';
import { Emoji } from 'emoji-mart';
import reactStringReplace from 'react-string-replace';
import customEmojisObj from '../../customEmojis'
import "./Message.scss";

const Message = ({ avatar, user, text, date, isMe, isReaded, attachments }) => (
  <div className={classNames("message", { "message--isme": isMe })}>
    <div className="message__content">
      {isMe && isReaded ? (
        <img
          className="message__icon-readed"
          src={readedSvg}
          alt="Readed icon"
        />
      ) : (
        <img
          className="message__icon-readed message__icon-readed--no"
          src={noReadedSvg}
          alt="No readed icon"
        />
      )}
      <div className="message__avatar">
        {/* <img className="avatar" src={avatar} alt={`Avatar ${user.username}`} /> */}
        <Avatar
          alt={`Avatar ${user.username}`}
          src={user.imgUrl}
        />
      </div>
      <div className="message__info">
        <div className="message__bubble">
          {text && (
            <p className="message__text">
            &#160;
        {reactStringReplace(text, /:(.+?):/g, (match, i) => (
            customEmojisObj[match]
            ?<img  className="emoji" key={i} style={{width:20,height:20,backgroundSize:'contain',backgroundImage:`url(${customEmojisObj[match].imageUrl})`}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
            :<img  className="emoji" key={i} style={{...Emoji({emoji:match,set:"twitter",size:20,css:true})}}  src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" alt={`:${match}:`} ></img>
          ))}
            &#160;
            </p>
          )}
        </div>
        <div className="message__attachments">
          {attachments &&
            attachments.map(item => (
              <div className="message__attachments-item">
                <img src={item.url} alt={item.filename} />
              </div>
            ))}
        </div>
        <span className="message__date">
          {distanceInWordsToNow(date, { addSuffix: true, locale: ruLocale })}
        </span>
      </div>
    </div>
  </div>
);

Message.defaultProps = {
  user: {}
};

Message.propTypes = {
  avatar: PropTypes.string,
  text: PropTypes.string,
  date: PropTypes.string,
  user: PropTypes.object,
  attachments: PropTypes.array
};

export default Message;