import React,{useEffect,useRef} from "react";
import PropTypes from "prop-types";
import { Empty } from "antd";
import CircularProgress from '@material-ui/core/CircularProgress';
import { Message } from "../";

const Messages = ({ items, userId, isLoading }) => {
  const massagesRef = useRef(null);
  useEffect(()=>{
    const bottomOffs = massagesRef.current.scrollHeight-massagesRef.current.scrollTop-massagesRef.current.offsetHeight
    console.log('bottomOffs',bottomOffs)
    if(bottomOffs<300){
      massagesRef.current.scrollTo({
        top: massagesRef.current.scrollHeight,
        behavior: "smooth"
      });
    }
  },[items])
  useEffect(()=>{
    massagesRef.current.scrollTo({
      top: massagesRef.current.scrollHeight,
    });
  },[isLoading])
  return (
    <div className="chat__dialog-messages" ref={massagesRef}>
      <div
        className={"messages"}
      >
        {isLoading ? (
          <div className="chat__dialog ant-empty"><CircularProgress color="secondary" /></div>
        ) : items && !isLoading ? (
          items.length > 0 ? (
            items.map(item => <Message 
              {...item}
              text={item.text}
              // avatar={item.user.imgUrl}
              isMe={userId==item.user.id} 
              key={item.id} 
              // user={item.user}
              date={item.createdAt} 
              />)
          ) : (
            <Empty description=" " />
          )
        ) : (
          <Empty description=" " />
        )}
      </div>
    </div>
  );
};

Messages.propTypes = {
  items: PropTypes.array
};

export default Messages;
