import React, { useState,useEffect } from "react";
import { Dialogs as BaseDialogs } from "../components";
import CircularProgress from '@material-ui/core/CircularProgress';
const Dialogs = ({ items, userId,selectedDialog, isLoading }) => {
  const [inputValue, setValue] = useState("");
  const [filtred, setFiltredItems] = useState(Array.from(items));
  useEffect(()=>{

    onChangeInput(inputValue)
    console.log('items',items.filter(el=>(el.user.username.toLowerCase().includes('test'))))
  },[items])
  const onChangeInput = value => {
    const text = value.toLowerCase()
    setFiltredItems(
      items.filter(el=>(el.user.username.toLowerCase().includes(text)))
      // items.filter(
      //   dialog =>
      //     dialog.user.username.toLowerCase().indexOf(value.toLowerCase()) >= 0
      // )
    );
    setValue(value);
  };
  if(isLoading){
    return (
      <div style={{
        height: '75%',
        'min-height': '75%',
        display: 'flex',
        'justify-content': 'center',
        'align-items': 'center',
        }}>
      <div className="ant-empty"><CircularProgress color="secondary" /></div>
      </div>
    );
  }
  return (
      <BaseDialogs
        userId={userId}
        items={filtred}
        selectedDialog={selectedDialog}
        onSearch={onChangeInput}
        inputValue={inputValue}
      />
  );
};

export default Dialogs;
